import { aPage } from '@ali/mor-core';
import { get } from '../../services';
aPage({
    data: {
        articles: {},
    },
    onLoad: function () {
        var _this = this;
        get('/sports/getSports').then(function (articles) {
            console.log('=====  articles', articles);
            _this.setData({ articles: articles[0] });
        });
    },
    //分享给好友
    onShareAppMessage: function () {
        var promise = new Promise(function (resolve) {
            setTimeout(function () {
                resolve({
                    title: '九霞裾'
                });
            }, 20);
        });
        return {
            title: '九霞裾',
            path: 'pages/shop/shop',
            promise: promise
        };
    },
    //转发到朋友圈
    onShareTimeline: function () {
        return {
            title: '快来看看'
        };
    },
});
